body {
    background-color: white;
    margin: 0;
}
.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.navbar {
    background-color: #333;
  }
  
  .navbar-container {
    display: flex;
    justify-content: space-between; 
    padding: 10px;
  }
  
  .logo-container{
    display: flex;
    align-items: center;
    margin-left: 50px;
  }
  .website-name {
    margin-left: 10px;
    font-size: 1.2em;
    font-weight: bold;
    color: white;
  }
  .navbar-logo,
  .navbar-logonu {
    height: 50px; /* Adjust as needed */
  }
  
  .navbar-links {
    align-items: center;
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .navbar-links li {
    margin: 0 10px;
  }
  
  .navbar-links a {
    text-decoration: none;
    color: #fff;
    font-size: 1em;
    transition: color 0.3s, background-color 0.3s, padding 0.3s;
  }
  
  .navbar-links a:hover {
    color: #333;
    background-color: #fff;
    padding: 5px 10px;
    border-radius: 5px;
  }
  